import {post} from '@/common/index'
export default {
	data() {
		return {
			loginInfo:false,
			data:{},
			statusBarHeight: getApp().globalData.statusBarHeight,
		}
	},
	onLoad(e) {
		var that=this
		post('/api/buy/order/info/info',String(e.id)).then((res)=>{
			if(res[1].data.code==200){
				that.data=res[1].data.data
			}else{
				toast(res[1].data.msg)
			}
			
		})
	},
	methods: {
		goBack:function(){
			uni.navigateBack({
				delta: 1
			});
		},
	}
}
